import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { EncrDecrService } from 'app/core/services/encr-decr-service.service';

@Component({
  selector: 'app-reset-password-landing-page',
  templateUrl: './reset-password-landing-page.component.html',
  styleUrls: ['./reset-password-landing-page.component.scss']
})
export class ResetPasswordLandingPageComponent implements OnInit {

  verificationCode = '';
  username = '';
  newPassword = '';
  confirmNewPassword = '';

  isShowUpdatePassword: boolean;
  hideNewPassword = true;
  hideConfirmNewPassword = true;

  passwordFormatChecker: FormGroup;

  constructor(
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private route: ActivatedRoute,
    private router: Router,
    private formBuilder: FormBuilder,
    private EncrDecr: EncrDecrService
  ) { }

  ngOnInit(): void {
    this.verificationCode = this.route.snapshot.paramMap.get('code');
    this.username = this.route.snapshot.paramMap.get('username');
    this.username = this.EncrDecr.get(this.username.replace(new RegExp('-', 'g'), '/'));

    this.setFormValidator();

    if (this.verificationCode) {
      this.validateVerificationCode();
    }
  }

  setFormValidator() {
    this.passwordFormatChecker = this.formBuilder.group({
      username: [''],
      newPassword: ['', [Validators.required, Validators.pattern('((?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,30})'), Validators.minLength(8)]],
      confirmNewPassword: ['', Validators.required],
    });
  }

  validateVerificationCode() {
    this.apiService.findByParam(ENDPOINTS.forgotPasswordVerification, this.verificationCode).subscribe(
      res => {
        this.isShowUpdatePassword = true;

        if (!this.isShowUpdatePassword) {
          this.sweetAlertService.customErrorMessage('Invalid verification code!');
        }
      }
    );
  }

  submitNewPassword() {
    const account = {
      username: this.username,
      password: this.newPassword
    };
    this.apiService.save(`${ENDPOINTS.newPasswordVerification}/${this.verificationCode}/new-password/`, account).subscribe(
      res => {
        console.log(res);
        this.sweetAlertService.success(res, 5);
        setTimeout(() => {
          this.goToLogin();
        }, 5000);
      }, err => {
        this.sweetAlertService.error(err);
      }
    );
  }

  goToLogin() {
    this.router.navigate(['/login']);
  }

}
